import React from 'react';

function Dom(){
    return (<div>
        <>
        <p>Virutal DOM(Document Object Model)</p>
        <p>: 개발자가 실제 DOM을 직접 조작하지 않고 UI를 갱신할 수 있도록 하는 기법</p>
        <p>Virtual DOM 과 실제 DOM</p>
        <p>
        <ul>
          <li>실제 DOM tree를 반여한 Virutal DOM tree 생성</li>
          <li>Virtual DOM 갱신</li>
          <li>Reconciliation 호출</li>
          <li>실제 DOM tree에서 필요한 부분만 갱신 </li>
        </ul>
      </p>
        
        </>
    </div>);

}

export default Dom;