import React from 'react';

function Component(){
    return (<div>
        <>
      <div>
        <p>Component pattern</p>
        <p>Presentaional component와 Container component의 조화</p>
        <p>Presentaional component은 UI를 rendering한다.</p>
        <p>Container component은 상태를 관리한다.</p>
      </div>

      <div>
        <p>Export and import</p>
        <p>Export를 component를 public으로 만든다.</p>
        <p>Default export는 default import와 작동한다.</p>
        <p>export default xxxx vs import xxxx from *****</p>
        <p>named export는 named import와 작동한다.</p>
        <p>export xxxx vs import &#123; xxxx &#125; from *****</p>
      </div>
      <div>
        <p>Props</p>
        <p>Component에게 전달되는 optional parameter</p>
        <p>Component의 속성들을 포함</p>
        
      </div>


        
        </>
    </div>);

}

export default Component;